import loadPage from '../pages'
import {Home, About, Contact} from '../pages'
import logo from '../../assets/images/logo.png'
import './navbar.css'

const homePressed = () => {
  return loadPage(<Home />)
}

const contactPressed = () => {
  return loadPage(<Contact />)
}

const aboutPressed = () => {
  return loadPage(<About />)
}

const Navbar = () => {
  return (
    <div className="global-navbar">
      <img src={logo} className="logo" alt="logo" />
      <div className='menu-buttons'>
        <button onClick={homePressed} > Home </button>
        <button onClick={contactPressed} > Contact </button>
        <button onClick={aboutPressed} > About </button>
      </div>
    </div>
  );
}

export default Navbar;
