import './homeImage.css'

let homeSegmentCount = 0;


const homeImage = (thisImage, thisTitle, thisText) => {
    let thisClass = 'image-section'
    homeSegmentCount += 1

    if (homeSegmentCount % 2 === 0) {
        thisClass = 'image-section light-section'
    }

    return <div className={thisClass}>
        <img src={thisImage} className='home-image' alt='example' />
        <div className='image-section-text'>
            <h3>
                {thisTitle}
            </h3>
            <p>
                {thisText}
            </p>
        </div>
    </div>
}


const incrementHomeSegmentCount = () => {
    homeSegmentCount += 1
}


const resetHomeSegmentCount = () => {
    homeSegmentCount = 0
}


export default homeImage
export {
    resetHomeSegmentCount,
    incrementHomeSegmentCount
}
