// MODULES
import {render} from 'react-dom'

// PAGES
import Home from './home/home'
import Contact from './contact/contact'
import About from './about/about'

const loadPage = (page) => {
    return render(
        <>
            {page}
        </>,
      document.getElementById('page-root')
    )
}

// EXPORT PAGES & LOAD MODULE
export default loadPage
export {
    Home,
    Contact,
    About,
}
